@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
* {
  overflow-x: hidden;
  font-family: Inter;
  line-height: 150%;
  color: black;
}
html {
  scroll-padding-top: 80px; /* height of your navbar */
}

.d-flex {
  display: flex;
}

.d-grid {
  display: grid;
}

.align-center{
	align-items: center;
}

.td-none {
  text-decoration: none;
  color: black;
}

.p-relative {
  position: relative;
}

.navbar-container {
  width: -webkit-fill-available;
  height: 80px;
  align-items: center;
  justify-content: space-between;
  /* border: 1px solid red; */
}

.nav-left {
  padding-left: 40px;
  /* font-size: 34px; */
  font-weight: 500;
  font-variant: small-caps;
	align-items: center;
}
.nav-left a {
  font-size: 34px;
  text-decoration: none;
	align-items: center;
	/* color: black; */
}

.nav-svg{
	margin-right: 1em;
	/* height: min-content; */
	align-items: center;
}

.str0 {
  stroke: #2b2a29;
  stroke-width: 75;
  stroke-miterlimit: 22.9256;
}
.fil0 {
  fill: none;
}

.nav-list {
  display: flex;
  margin: 0 48px 0 0;
  font-size: 20px;
}

.nav-list:hover {
  /* text-decoration: underline; */
	background-color: #d3d3d3;
	/* border-width: 2ex 1.25ex 0.5ex; */
	border-radius: 3px;
}
.nav-right a{
	text-decoration: none;
}
.active-scroll-spy {
  border-bottom: 2px solid black;
}


.title-container {
  width: 530px;
  height: fit-content;
  margin: 60px 0 24px 60px;
}

.home-container {
  margin-top: 80px;
}

.home-title {
  font-size: 45px;
  font-weight: 700;
}

.home-desc {
  font-size: 18px;
  color: #626262;
}

.projects-container {
  grid-template-columns: repeat(2, 1fr);
  margin: 64px 60px;
  gap: 35px;
}

.project-item-container {
  position: relative;
  overflow: hidden;
  transition: all 0.1s ease;
  border-radius: 5px;
  width: 680px;

  padding-bottom: 1em;
}

.project-item {
  position: relative;
  overflow: hidden;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  justify-content: flex-start;
  flex-direction: column;
  padding: 5px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(65, 90, 119, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-item-container:hover .project-item::before {
  opacity: 0.5;
}

.project-img {
  /* width: 100%; */
	height: 320px;
	background-size: contain;
  background-color: #fff;
  justify-content: center;
  border-radius: 5px;
  overflow: hidden;
	background-repeat: no-repeat;
	background-position: center;
}


.item-title {
  margin: 20px 0 0 0;
  font-size: 20px;
  font-weight: 500;
}

.item-desc {
  color: #626262;
  margin-bottom: 3rem;
}

.footer-container {
  height: 200px;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  box-shadow: 0px -5px 20px #1e1e1e;
  margin: 128px 0 0 0;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.footer-title {
  font-size: 34px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: "Montserrat";
  color: white;
	height: inherit;
	align-items: center;
}

.footer-links-container {
	width: -webkit-fill-available;
  align-items: center;
  justify-content: flex-end;
}

.footer-links {
	width: 150px;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
  height: fit-content;
}

.error-page {
  height: calc(100vh - 280px);
  text-align: center;
  font-size: 24px;
}

.about-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  overflow: hidden;
  align-items: flex-start;
  scrollbar-width: none;
}
.about-contents {
  width: 800px;
  height: fit-content;
  margin: 60px 0 0 60px;
}

.generic-title {
  font-size: 43px;
  font-weight: 700;
}

.generic-subheading {
  margin: 18px 0;
  font-size: 24px;
  color: crimson;
}

.generic-body {
  font-size: 18px;
  line-height: 150%;
}

.about-body ul {
  list-style-type: disc;
  list-style-position: inside;
}

.about-svg {
  position: relative;
  top: 100px;
  right: -50px;
  right: -120px;
  width: 500px;
  scrollbar-width: none;
}

.contact-container {
  width: -webkit-fill-available;
}

.contact-contents {
  width: 800px;
  height: fit-content;
  margin: 60px 0 0 60px;
}

.generic-body a:hover {
  text-decoration: underline;
}

.project-link-tag {
  width: fit-content;
  height: fit-content;
}

.footer-container span {
  color: crimson;
}

@media only screen and (max-width: 1500px) {
  .projects-container {
    grid-template-columns: auto;
    justify-content: center;
  }
  .about-container {
    flex-direction: column-reverse;
    align-items: center;
  }
  .about-contents {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 85vw;
    margin: 60px 0;
  }
  .about-svg {
    position: relative;
    width: 300px;
    top: 100px;
    right: 0;
  }
  .contact-container {
    justify-content: center;
  }
  .contact-contents {
    width: 65vw;
  }
}

@media screen and (max-width: 850px) {
  .nav-left {
    padding-left: 40px;
  }
  .nav-left a {
    font-size: 30px;
  }
  .nav-right a {
    font-size: 16px;
  }
  .nav-list {
    margin: 0 28px 0 0;
  }
  .project-item-container {
    width: 500px;
  }
  .project-img {
    height: 240px;
		/* background-size:'cover'; */
		background-position-x: center;
		background-position-y: center;
		background-repeat: no-repeat;
  }
  .about-container {
    align-items: center;
    /* width: -webkit-fill-available; */
  }
  .about-contents {
    /* width: inherit; */
    /* margin-right: 60px; */

    width: 85vw;
    margin: 60px 0;
    text-align: justify;
  }
  .footer-title {
    font-size: 30px;
  }
}
@media screen and (max-width: 600px) {
  .nav-left a {
    font-size: 24px;
  }
  .nav-right a {
    font-size: 15px;
  }
  .home-title {
    font-size: 40px;
  }
  .home-desc {
    width: 80vw;
  }
  .footer-title {
    font-size: 24px;
  }
  .home-container {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
@media screen and (max-width: 500px) {
  .nav-left a {
    font-size: 20px;
  }
  .nav-right a {
    font-size: 12px;
  }
  .home-title {
    font-size: 30px;
  }
  .home-desc {
    font-size: 16px;
  }
  .about-container {
    align-items: center;
  }
  .about-contents,
  .generic-body {
    width: 95vw;
  }
  .about-contents {
    margin: 70px 10px;
  }
  .contact-contents {
    justify-content: flex-start;
    margin: 0px 0px;
    width: fit-content;
  }
  .generic-title {
    font-size: 30px;
  }
  .generic-subheading {
    font-size: 18px;
  }
  .generic-body {
    font-size: 16px;
  }
  .title-container {
    margin: 60px 0px 0 20px;
  }
  .home-container {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .title-container {
    width: fit-content;
  }
  .nav-left a {
    font-size: 20px;
  }
  .nav-right a {
    font-size: 9px;
  }
  .nav-left {
    height: fit-content;
    padding-left: 20px;
  }
  .footer-title {
    font-size: 20px;
  }
  .project-item-container {
    width: 95vw;
  }
}
@media screen and (max-width: 430px) {
  .nav-left a,
  .footer-title {
    font-size: 15px;
  }
  .footer-container {
    height: 150px;
  }
  .project-item-container {
    width: 375px;
  }
	.nav-svg{
		width: 35px;
	}
	.nav-list{
		margin-right: 20px;
	}
}
@media screen and (max-width: 380px) {
	.nav-svg{
		width: 30px;
	}
  .project-item-container {
    width: 350px;
  }
	.nav-list{
		margin-right: 18px;
	}
	.footer-links{
		width: 90px;
	}
	.footer-links img{
		width: 32px;
	}
}
@media screen and (max-width: 350px) {
  .project-item-container {
    width: 95vw;
  }
	.nav-svg{
		margin-right: 0.4em;
		width: 25px;
	}
	.nav-left a{
		font-size: 13px;
	}
	.nav-list{
		margin-right: 15px;
	}
	.home-title{
		font-size: 20px;
	}
	.generic-title{
		font-size: 20px;
	}
	.generic-subheading{
		font-size: 14px;
	}
	.footer-links{
		width: 90px;
	}
	.footer-links img{
		width: 32px;
	}
}